.sb-top-nav{
    margin-bottom: 30px;
    justify-content: center;
    gap: 20px;
    .sb-logo {
        width: 170px;
        height: 65px;
        display: flex;
        align-items: center;
        padding: 12px 14px;
        background-image: linear-gradient(180deg, #FFFFFF 0%, #EFF1FF 122.22%);
        box-shadow: 0px 2px 2px #D6D8E0;
        border-radius: 6px;
        img{
            max-width: 100%;
            height: auto;
            object-fit: cover;
        }
    }
    
    .sb-panel-tab{
        margin-bottom: 0;
    }
}
