.widget-tabs {
    margin: 20px;
}

.tab-nav {
    list-style: none;
    padding: 0;
    display: flex;
    margin-bottom: 20px;
}

.tab-nav li {
    margin-right: 20px;
    padding: 10px 20px;
    background: #FFFFFF;
    box-shadow: 0px 2px 2px #E0E2EA;
    border-radius: 6px;
    position: relative;
    font-family: var(--sb-ff-heading);
    color: var(--sb-heading-primary);
    margin-top: 0px;
    line-height: 1.2;
    font-weight: 500;
    font-size: 14px;
    cursor: pointer;
    :hover {
        z-index: 1;
        box-shadow: 0px 0px 2px #1b48ff;
        cursor: pointer;
    }
    &.active {
        display: inline-block;
        font-size: 15px;
        font-weight: 600;
        color: var(--sb-common-white);
        border-radius: 6px;
        background: var(--sb-theme-primary);
        padding: 10px 26px;
    }
}