@use '../utils/' as *;

@import url($font-url);

*{
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

/*---------------------------------
	typography css start 
---------------------------------*/
.components-notice {
    margin-bottom: 20px;
    margin-top: 15px;
}

.toplevel_page_shopbuild #wpcontent{
	background-color: #e9ebf7;
	.notice{
		display: none;
	}
}
#pure-wc-shopbuild-admin-app{
	
	font-family: var(--sb-ff-body);
	font-size: 16px;
	font-weight: normal;
	color: var(--sb-text-body);
	line-height: 1.6;


	h1,
	h2,
	h3,
	h4,
	h5,
	h6 {
		font-family: var(--sb-ff-heading);
		color: var(--sb-heading-primary);
		margin-top: 0px;
		line-height: 1.2;
		@include transition(color);
	}

	a{
		text-decoration: none;

		&:focus{
			box-shadow: none;
			outline: 0;
			color: #FFFFFF;
		}
	}

	p {
		margin-top: 0;
		font-family: var(--sb-ff-p);
		font-weight: 400;
		color: var(--sb-text-body);
	}
}



