button:hover{
	cursor: pointer;
}

button:focus{
    outline: 0; 
}

.#{$theme-prefix}-w-img{
	& img{
		width: 100%;
	}
}


.#{$theme-prefix}-btn{
	display: inline-block;
	font-size: 15px;
	font-weight: 500;
	color: var(--sb-theme-primary)!important;
	border-radius: 6px;
	background-color: rgba($color: $theme-primary, $alpha: .07)!important;
	padding: 10px 26px;
	@extend %transition;
	&:hover{
		background-color: var(--sb-theme-primary)!important;
		color: var(--sb-common-white)!important;
	}
}

.#{$theme-prefix}-btn-fill{
	display: inline-block;
	font-size: 15px;
	font-weight: 600;
	color: var(--sb-common-white);
	border-radius: 6px;
	background: var(--sb-theme-primary);
	padding: 10px 26px;
	@extend %transition;
	&:hover{
		background-color: #4618E1;
		color: var(--sb-common-white);
		box-shadow: 0px 6px 8px rgba(39, 20, 101, 0.2);
	}
}




