	margin-bottom: 50px;
}

.mb-55{
	margin-bottom: 55px;
}

.mb-30{
	margin-bottom: 30px;
}

.pb-55{
	padding-bottom: 55px;
}

.#{$theme-prefix}-toggle-switch{
	&:hover{
		cursor: pointer;
	}
	&.active{
		& label{
			background-color: var(--sb-theme-primary);
			&::after{
				right: 3px;
				left: 54%;
				transition: 0.25s cubic-bezier(0.785, 0.135, 0.15, 0.86);
				transition-property: all;
				transition-delay: 0s;
				transition-property: left, right;
				transition-delay: .08s, 0s;
			}
		}
	}
	& span{
		font-weight: 500;
		font-size: 15px;
		color: var(--sb-common-black);
		@media #{$xs}{
			font-size: 14px;
		}

	}
	&-input{
		display: none;
		&[type="checkbox"]{
			display: none;
		}
	}

	& label{
		display: inline-block;
		width: 54px;
		height: 28px;
		border-radius: 20px;
		background-color: #CAD2EF;
		position: relative;
		margin: 0 13px;
		@extend %transition;
		
		@media #{$xs}{
			margin: 0 12px;
		}
		&:hover{
			cursor: pointer;
		}
		&::after{
			display: inline-block;
			position: absolute;
			content: '';
			left: 3px;
			top: 3px;
			bottom: 3px;
			right: 54%;
			border-radius: 50%;
			@include gradient((180deg, #FFFFFF 0%, #D6DCEF 100%));
			box-shadow: 1px 2px 3px rgba(18, 20, 32, 0.3);
			transition: 0.25s cubic-bezier(0.785, 0.135, 0.15, 0.86);
			transition-property: all;
			transition-delay: 0s;
			transition-property: left, right;
			transition-delay: 0s, .08s;
		}
	}
}

.#{$theme-prefix}-toggle-switch-2{
	$self : &;
	& input{
		display: none;

		&:checked{
			& ~ {
				& #{$self}{
					&-bg{
						background-color: var(--sb-theme-primary);
					}
					&-text{
						&.on{
							visibility: visible;
							opacity: 1;
						}
						&.off{
							visibility: hidden;
							opacity: 0;
						}
					}
					&-handle{
						right: 3px;
						left: 61%;
						transition: 0.25s cubic-bezier(0.785, 0.135, 0.15, 0.86);
						transition-property: all;
						transition-delay: 0s;
						transition-property: left, right;
						transition-delay: .08s, 0s;
					}
				}
			}
		}

	}
	& label{
		display: inline-block;
		width: 56px;
		height: 24px;
		border-radius: 20px;
		position: relative;
		overflow: hidden;
		&:hover{
			cursor: pointer;
		}
	}
	&-text{
		font-size: 12px;
		text-transform: uppercase;
		font-weight: 600;
		color: var(--sb-common-white);
		position: absolute;
		top: 2px;
		left: 8px;
		z-index: 1;
		visibility: hidden;
		opacity: 0;
		@extend %transition;
		&.off{
			left: auto;
			right: 7px;
			visibility: visible;
			opacity: 1;
			color: var(--sb-common-black);
		}
	}
	&-handle{
		display: inline-block;
		position: absolute;
		content: '';
		left: 3px;
		top: 3px;
		bottom: 3px;
		right: 61%;
		border-radius: 50%;
		@include gradient((180deg, #FFFFFF 0%, #D6DCEF 100%));
		box-shadow: 1px 2px 3px rgba(18, 20, 32, 0.3);
		transition: 0.25s cubic-bezier(0.785, 0.135, 0.15, 0.86);
		transition-property: all;
		transition-delay: 0s;
		transition-property: left, right;
		transition-delay: 0s, .08s;
		z-index: 1;
	}
	&-bg{
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: #CAD2EF;
		@extend %transition;
	}
}

.#{$theme-prefix}-radio{
	$radio : &;
	& label{
		display: inline-block;

		&:hover{
			cursor: pointer;
		}
	}
	&-toggle{
		&:hover{
			cursor: pointer;
		}
		& input{
			display: none;

			&:checked{
				& ~ {
					#{$radio}{
						&-handle{
							&::after{
								border-color: var(--sb-theme-primary);
							}
							&::before{
								background-color: var(--sb-theme-primary);;
							}
						}
					}
				}
			}
		}
		
	}
	&-handle{
		display: inline-block;
		font-size: 14px;
		color: #5A5A64;
		padding-left: 28px;
		position: relative;

		&::after{
			position: absolute;
			top: 1px;
			left: 0;
			content: '';
			width: 18px;
			height: 18px;
			border-radius: 50%;
			border: 1px solid rgba($color: #0D0432, $alpha: .3);
			@extend %transition;
		}

		&::before{
			position: absolute;
			top: 7px;
			left: 6px;
			content: '';
			width: 8px;
			height: 8px;
			border-radius: 50%;
			background-color: rgba($color: #0D0432, $alpha: .3);
			@extend %transition;
		}
	}
}