@use '../utils/' as *;

/* Panel Css Start */

.#{$theme-prefix}-panel{
    background-color: #E9EBF7;
    &-inner{
        padding: 38px 26px 70px;
        background-color: #E9EBF7;

        @media #{$xs}{
            padding: 30px 20px 35px;
        }
    }
    &-wrapper{
        background-color: #F6F8FF;
        padding: 40px;
        border-radius: 6px;

        @media #{$sm}{
            padding: 20px;
        }
        @media #{$xs}{
            padding: 10px 10px 30px;
        }
    }

    &-tab{
        padding: 12px 14px;
        @include gradient((180deg, #FFFFFF 0%, #EFF1FF 122.22%));
        box-shadow: 0px 2px 2px #D6D8E0;
        border-radius: 6px;
        margin-bottom: 30px;
    }

    &-tab-btn{
        @extend %ff-poppins;
        font-weight: 500;
        font-size: 16px;
        color: var(--sb-common-black);
        background-color: transparent;
        border: 0;
        outline: 0;
        padding: 7px 41px;
        padding-left: 38px;
        border-radius: 4px;
        @extend %transition;
        position: relative;
        @media #{$xs}{
            text-align: left;
            padding-left: 20px;
            padding-right: 20px;
            width: 100%;
        }
        @media #{$xl}{
            padding: 7px 30px;
        }

        &::after{
            position: absolute;
            content: '';
            right: 0;
            top: 50%;
            width: 1px;
            height: 30px;
            background-color: #D3D8EA;
            @include transform(translateY(-50%));

            @media #{$xs}{
                display: none;
            }
        }

        &:last-child{
            &::after{
                display: none;
            }
        }
        & span{
            margin-right: 10px;
            & svg{
                @include transform(translateY(2px));
            }
        }
        

        &.active{
            background-color: var(--sb-theme-primary);
            color: var(--sb-common-white);
            box-shadow: 0px 6px 8px rgba(39, 20, 101, 0.2);
            &:hover{
                color: var(--sb-common-white);
            }

            &::after{
                background-color: transparent;
            }
        }

        &:hover{
            color: var(--sb-theme-primary);
        }
    }
}

.components-panel__body.is-opened {
    padding: 16px;
    // padding-top: 0;
}