@use '../utils/' as *;

/* Review Css Start */

.#{$theme-prefix}-review{
    &-area{
        padding-bottom: 58px;
    }
    &-item{
        background: #FFFFFF;
        box-shadow: 0px 1px 4px #D4D8EB;
        border-radius: 6px;
        padding: 60px 60px 60px;

        @media #{$lg}{
            padding: 50px;
        }
        @media #{$xs}{
            padding: 20px;
        }
    }
    &-title{
        font-weight: 600;
        font-size: 22px;
        color: #041137;
        margin-bottom: 10px;
    }
    &-icon{
        margin-right: 60px;

        @media #{$sm, $xs}{
            margin-bottom: 30px;
            margin-right: 0;
        }
    }
    &-content{
        & p{
            margin-bottom: 30px;

            @media #{$lg, $md, $sm, $xs}{
               & br{
                    display: none;
               }
            }
        }
    }
    &-btn{
        &-wrapper{
            & .sb-btn{
                font-size: 14px;
                font-weight: 600;
                padding: 11px 26px;
            }
        }
    }
}