@use '../utils/' as *;

#pure-wc-shopbuild-tmpl-popup{
    &.sb-editing{
        & .sb-new{
            display: none!important;
        }
    }
    &.sb-adding{
        & .sb-exists{
            display: none!important;
        }
    }
}

/* Offcanvas Css Start */

.#{$theme-prefix}-offcanvas{
    &-area{
        position: fixed;
        top: 0;
        right: 0;
        height: 100%;
        width: 700px;
        background-color: var(--sb-common-white);
        @include transform(translateX(calc(100% + 80px)));
        -webkit-transition: transform 0.45s ease-in-out, opacity .45s ease-in-out;
        -moz-transition: transform 0.45s ease-in-out, opacity .45s ease-in-out;
        transition: transform 0.45s ease-in-out, opacity .45s ease-in-out;
        z-index: 999;
       
        overflow-y: scroll;
        overscroll-behavior-y: contain;
        scrollbar-width: none;

        &::-webkit-scrollbar {
            display: none; /* for Chrome, Safari, and Opera */
        }
        &.sb-offcanvas-opened{
            @include transform(translateX(0));
            opacity: 1;
        }


        @media #{$sm}{
            width: 500px;
        }

        @media #{$xs}{
            width: 300px;
        }
    }
    &-wrapper{
        height: 100%;
    }
    &-close-btn{
        color: var(--sb-common-black);
        background-color: transparent;
        border: 0;
        outline: 0;
        & svg{
           @include transform(translateY(1px));
           margin-right: 10px;
        }
    }
    &-header{
        padding: 13px 30px;
        box-shadow: 0px 2px 3px rgba(11, 15, 45, 0.1);
        position: absolute;
        top: 30px;
        left: 0;
        right: 0;
        width: 100%;
        z-index: 11;
        background: #FFFFFF;
        & p{
            font-size: 15px;
            font-weight: 500;
            color: #82808A;
            margin-bottom: 0;
            margin-top: 0;
        }
    }
    &-content{
        padding: 0 30px;
        padding-top: 80px;
    }
    &-option{
        &-item{
            padding: 25px 0 23px;
            border-bottom: 1px solid rgba($color: #0D0432, $alpha: .1);
            &:last-child{
                border-bottom: 0;
                margin-bottom: 8px;
            }
        }
        &-content{
            @media #{$sm, $xs}{
                margin-bottom: 15px;
            }
            & p{
                font-size: 14px;
                color: #5A5A64;
                margin-bottom: 0;
                margin-top: 0;
            }
        }
        &-title{
            font-size: 15px;
            margin-bottom: 2px;
            &.font-semibold{
                font-weight: 600;
            }
        }
        &-action{
            @media #{$sm, $xs}{
                width: 100%;
            }
        }

        .components-base-control {
            width: 100%;
        }
        
    }
    &-radio{
        &-wrapper{
            & .sb-radio-toggle{
                &:not(:last-child){
                    margin-right: 40px;
                }
            }
        }
    }
    &-select{
        width: 270px;

        @media #{$sm, $xs}{
            width: 100%;
        }
    }
    &-input{
        width: 270px;
        @media #{$sm, $xs}{
            width: 100%;
        }
        & input{
            outline: 0;
            width: 100%;
            height: 42px;
            border-radius: 4px;
            border: 1px solid rgba($color: #0D0432, $alpha: .12);
            padding: 0 20px;
            @extend %transition;
            box-shadow: none;
            outline: 0;
            &:focus{
                border-color: var(--sb-theme-primary);
            }
        }
        & input::placeholder{
            color: #bbb;
        }

        & textarea{
            width: 100%;
            height: 110px;
        }
    }
    &-section{
        &-title{
            padding: 18px 25px;
            background-color: rgba($color: #0AB06B, $alpha: .08);
            border-radius: 6px;
            & h3{
                font-size: 17px;
                margin-bottom: 0;
            }
        }
    }
    &-color{
        $color : &;
        width: 270px;

        @media #{$sm, $xs}{
            width: 100%;
            flex-wrap: wrap;
        }

       
        & > div{
            &:not(:last-child){
                margin-right: 6px;
            }
        }
        &-select{
            line-height: 1;

            &.heading-color{
                #{$color}-select{
                    &-palate{
                        background-color: #DCD7EF;
                    }
                }
            }
            & label{
                display: inline-block;
                width: 123px;
                height: 36px;
                line-height: 36px;
                border-radius: 4px;
                border: 1px solid rgba($color: #0D0432, $alpha: .12);
                position: relative;

                &:hover{
                    cursor: pointer;
                }
            }
            & input{
                display: none;
            }

            &-text{
                font-size: 12px;
                color: #605E67;
                position: absolute;
                top: 50%;
                right: 11px;
                line-height: 1;
                @include transform(translateY(-50%));
            }
            &-palate{
                display: inline-block;
                width: 34px;
                height: 34px;
                border-radius: 3px;
                background-color: var(--sb-theme-primary);
            }
        }
        &-input{
            & input{
                width: 80px;
                height: 36px;
                outline: 0;
                border: 1px solid rgba($color: #0D0432, $alpha: .12);;
                color: #0D0432;
                padding: 0 11px;
                border-radius: 4px;
                @extend %transition;
                &:focus{
                    border-color: var(--sb-common-black);
                }
            }
        }
        &-btn{
            @media #{$xs}{
                margin-top: 10px;
            }
            & button{
                height: 36px;
                line-height: 34px;
                font-size: 12px;
                font-weight: 500;
                color: var(--sb-theme-primary);
                padding: 0 10px;
                border: 1px solid var(--sb-theme-primary);
                border-radius: 4px;
                background-color: rgba($color: #5E2EFF, $alpha: .06);
                @extend %transition;
                &:hover{
                    background-color: var(--sb-theme-primary);
                    border-color: var(--sb-theme-primary);
                    color: var(--sb-common-white);
                }
            }
        }
    }
    &-btn{
        
        &-reset{
            font-size: 15px;
            font-weight: 500;
            color: rgba($color: #0D0432, $alpha: .2);
            border-radius: 6px;
            border: 1px solid rgba($color: #0D0432, $alpha: .2);
            display: inline-block;
            padding: 6px 25px 8px;
            text-align: center;
            text-decoration: none;
            @extend %transition;

            &:hover{
                background-color: var(--sb-common-black);
                border-color: var(--sb-common-black);
                color: var(--sb-common-white);
            }
        }
        &-fill{
            font-size: 15px;
            font-weight: 600;
            color: var(--sb-common-white);
            border-radius: 6px;
            border: 1px solid var(--sb-theme-primary);
            display: inline-block;
            padding: 7px 25px;
            text-align: center;
            text-decoration: none;
            background-color: var(--sb-theme-primary);
            @extend %transition; 
            &:hover{
                background-color: var(--sb-common-black);
                border-color: var(--sb-common-black);
                color: var(--sb-common-white);
            }
        }
    }
    &-footer{
        margin-top: 30px;
        padding: 20px 30px;
        background-color: var(--sb-common-white);
        box-shadow: 0px -1px 4px rgba(11, 15, 45, 0.1);
        position: sticky;
        bottom: 0;
        left: 0;
        right: 0;
        width: 100%;
        z-index: 11;
        &-btn{
            &-wrapper{
                & a,
                & button{
                    width: calc(50% - 5px);
                    &:not(:last-child){
                        margin-right: 10px;
                        @media #{$xs}{
                           margin-right: 0;
                           margin-bottom: 10px;
                        }
                    }

                    @media #{$xs}{
                        width: 100%;
                    }
                }
            }
        }
    }
}

.#{$theme-prefix}-body-overlay{
    background-color: var(--sb-common-black);
	height: 100%;
	width: 100%;
	position: fixed;
	top: 0;
	z-index: 99;
	left: 0;
	opacity: 0;
	visibility: hidden;
    @extend %transition;
    
    &.opened {
        opacity: 0.4;
        visibility: visible;
    }
}


#pure-wc-shopbuild-tmpl-popup{
    & .sb-offcanvas-input,
    & .sb-offcanvas-select{
        width: 100%;
        max-width: 100%;
    }
    & .sb-offcanvas-input input{
        box-shadow: none;
        outline: 0;
    }

    .sb-offcanvas-option-title{
        margin-bottom: 0;
    }

    .sb-offcanvas-option-content{
        margin-bottom: 15px;
    }

    .nice-select{
        & .list{
            z-index: 1;

            & .option{
                &.selected.focus{
                    color: var(--sb-theme-primary);
                }
                &.selected{
                    font-weight: 500;
                }
            }
        }

        &:active,
        &.open,
        &:focus{
            border-color: var(--sb-theme-primary);
        }
    }

    & .sb-offcanvas-btn-fill{
        padding: 10px 25px;
    }

    & .sb-offcanvas-btn-reset{
        padding: 10px 25px 10px;

    }

    & a:focus{
        box-shadow: none;
        outline: 0;
    }
    
}


.preview-template {
    border: 1px solid #efefef;
    padding: 15px;
    margin-top: 30px;
    .invoice-head {
        display: flex;
        justify-content: space-between;
        .logo{
            width: 180px;
            img{
                max-width: 100%;
            };
        }
        .right-info {
            text-align: end;
        }
        p{
            margin: 0;
        }
    }

    .invoice-table {
        margin-top: 40px;
        table{
            width: 100%;
            td{
                text-align: left;
                padding: 10px;
                font-size: 13px;
            }
            th{
                text-align: left;
                padding: 10px;
                background-color: #f9f9f9;
            }
            tbody{
                border-bottom: 1px solid #ddd;
            }
        }
    }

    .invoice-footer{
        background-color: #f9f9f9;
        text-align: center;
        padding: 20px;
        p{
            margin: 0;
        }
    }
}

.sb-offcanvas-option.sb-video-guide {
    position: relative;
    border: 1px solid #ccc;
    padding-top: 40px;
    margin-top: 15px;
    .sb-video-close-btn{
        position: absolute;
        top: 10px;
        right: 10px;
        padding: 5px;
        cursor: pointer;
    }
}