.sb-modal-wrapper{
    background: #00000056;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow-x: hidden;
    overflow-y: auto;
    outline: 0;
    z-index: 2;

    .sb-modal-dialog{
        width: 90%;
        max-width: 700px;
        margin: 2rem auto;
        margin-top: 150px;
        background: #fff;
        padding: 1.5rem;
        border-radius: 10px;
        position: relative;
        transition: 0.3s all;

        button{
            background: transparent;
            border: none;
            outline: none;
            position: absolute;
            top: 10px;
            right: 10px;

            :focus{
                border: none;
            }
        }

        iframe{
            width: 100%;
            height: 355px;
            border-radius: 10px;
        }
    }
}