.sb-toggle-switch.active label::after {
  right: 3px;
  left: 54%;
  transition: 0.25s cubic-bezier(0.785, 0.135, 0.15, 0.86);
  transition-property: all;
  transition-delay: 0s;
  transition-property: left, right;
  transition-delay: 0.08s, 0s;
