.sb-toggle-switch label::after {
  display: inline-block;
  position: absolute;
  content: "";
  left: 3px;
  top: 3px;
  bottom: 3px;
  right: 54%;
  border-radius: 50%;
  background-image: -webkit-linear-gradient(180deg, #FFFFFF 0%, #D6DCEF 100%);
