}
.sb-toggle-switch-2-text {
  font-size: 12px;
  text-transform: uppercase;
  font-weight: 600;
  color: var(--sb-common-white);
  position: absolute;
  top: 2px;
  left: 8px;
  z-index: 1;
  visibility: hidden;
  opacity: 0;
}
.sb-toggle-switch-2-text.off {
  left: auto;
  right: 7px;
  visibility: visible;
  opacity: 1;
  color: var(--sb-common-black);
}
.sb-toggle-switch-2-handle {
  display: inline-block;
  position: absolute;
  content: "";
  left: 3px;
  top: 3px;
  bottom: 3px;
  right: 61%;
  border-radius: 50%;
  background-image: -webkit-linear-gradient(180deg, #FFFFFF 0%, #D6DCEF 100%);
  background-image: -moz-linear-gradient(180deg, #FFFFFF 0%, #D6DCEF 100%);
  background-image: -ms-linear-gradient(180deg, #FFFFFF 0%, #D6DCEF 100%);
