@use '../utils/' as *;

/* Intro Css Start */

.#{$theme-prefix}-intro{
    $self : &;
    &-wrapper{
        background-color: #F6F8FF;
        border-radius: 6px;
    }
    &-banner{
        margin-bottom: 100px;
        & img{
            border-radius: 6px;
        }
    }

    &-item{
        background: var(--sb-common-white);
        box-shadow: 0px 2px 2px #E0E2EA;
        border-radius: 6px;
        padding: 48px 40px 56px;
        margin-bottom: 30px;
        position: relative;

        @media #{$lg}{
            padding: 38px 30px 46px;
        }

        @media #{$sm}{
            padding: 28px 30px 36px;
        }
        @media #{$xs}{
            padding: 40px 24px 40px;
        }

        &-big{
            #{$self}{
                &-content{
                    & p{
                        padding-right: 60px;

                        @media #{$sm, $xs}{
                            padding-right: 0;
                        }
                    }
                }
            }
        }

        & .sb-btn{
            width: 170px;
            text-align: center;
            padding: 8px 10px;
        }
    }
    &-icon{
        margin-right: 30px;

        @media #{$xs}{
            margin-bottom: 30px;
            margin-right: 25px;
        }
    }
    &-title{
        font-size: 24px;
        font-weight: 600;
        color: var(--sb-heading-secondary);
        margin-bottom: 10px;

        @media #{$xs}{
            font-size: 20px;
        }
    }
    &-content{
        & p{
            margin-bottom: 32px;
            font-size: 16px;
            @media #{$xs}{
                font-size: 15px;
            }
        }
    }
    &-thumb{
        position: absolute;
        top: 5%;
        right: 8%;

        @media #{$md}{
            position: static;
            margin-top: 45px;
            margin-left: 45px;
        }
        @media #{$sm, $xs}{
            position: static;
            margin-top: 45px;
        }

        & img{
            @media #{$xs}{
                width: 100%;
            }
        }
    }
}